body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.logo {grid-area:logo;}
.rank {grid-area:rank;}
.signout {grid-area:signout;}
.faceDetect {grid-area:faceDetect;}

.grid-container {
  display: grid;
  grid-template-areas: 
  "logo rank rank signout"
  "faceDetect faceDetect faceDetect faceDetect";
  grid-gap: 10px;
}
.imgContainer {
	position: relative;
	display: inline-block;
}

.boundingBox {
	position: absolute; 
	display:none;
}

.boundingBox.active {
	position: absolute; 
	display:block;
	border: 4px solid blue;
}

#imgFace {
	width: 400px;
	height: auto;
}
